'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['text!./view.html', 'globals', 'knockout', 'app/data-context-labels'], factory);
    }
})(function (template, globals, ko, ctx) {

    function ViewModel(params) {

        var self = this;

        this.email = globals.email;
        this.emailLink = 'mailto:' + this.email;
        this.labels = ko.observableArray([]);

        ctx.labels.getSummaries().then(function (data) {

            self.labels(data);
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});